import {
  ROOMS
} from '../mutation-types'

import * as actions from './actions'
import * as getters from './getters'

const state = {
  rooms: {}
}

const mutations = {
  [ROOMS] (state, data) {
    state.rooms = data
  }
}

export default {
  state: state,
  mutations: mutations,
  actions: actions,
  getters: getters
}



